






































































































































import { Vue, Component, Prop } from "vue-property-decorator";
import {
  settingsModule,
  authModule,
  notificationModule
} from "@/store/modules";
import { isEmail } from "validator";
import { SETUP_PREFILL_FORM, SETUP_PREFILL_DATA } from "@/constants";
import { Language } from "@/shared";

@Component
export default class HomeWizard extends Vue {
  @Prop({
    type: Boolean,
    default: false
  })
  admin!: boolean;

  e1 = 0;
  username = "";
  password = "";
  firstName = "";
  lastName = "";
  email = "";
  showPasswordPlaintext = false;
  loading = false;

  created() {
    if (SETUP_PREFILL_FORM) {
      Object.assign(this, SETUP_PREFILL_DATA);
    }
  }

  validateFirstForm() {
    if ((this.$refs.firstPageForm as any).validate()) {
      this.e1 = 3;
    }
  }

  get validateRules() {
    return {
      password: [v => !!v || this.$t("wizard.form.password-required")],
      username: [v => !!v || this.$t("wizard.form.username-required")],
      firstName: [v => !!v || this.$t("wizard.form.first-name-required")],
      lastName: [v => !!v || this.$t("wizard.form.last-name-required")],
      email: [
        v => !!v || this.$t("wizard.form.email-required"),
        v => isEmail(v) || this.$t("wizard.form.invalid-email")
      ]
    } as { [index: string]: ((v: string) => boolean | string)[] };
  }

  get progress() {
    let score = this.password.length * 2.7;
    if (this.hasUppercaseLetters) {
      score += 10;
    }
    if (this.hasLowercaseLetters) {
      score += 10;
    }
    if (this.hasNumbers) {
      score += 15;
    }
    if (this.hasSymbols) {
      score += 15;
    }
    if (this.areWordsRepeated) {
      score -= 20;
    }
    if (this.areCharactersRepeated) {
      score -= 30;
    }
    if (!this.isLongEnough) {
      score -= 30;
    }
    return Math.min(100, Math.max(0, score));
  }
  get color() {
    return ["error", "warning", "success"][Math.floor(this.progress / 40)];
  }
  get hasLowercaseLetters() {
    return /[a-z]/.test(this.password);
  }
  get hasUppercaseLetters() {
    return /[A-Z]/.test(this.password);
  }
  get hasNumbers() {
    return /[0-9]/.test(this.password);
  }
  get hasSymbols() {
    return /\W+/.test(this.password);
  }
  get isLongEnough() {
    return this.password.length >= 8;
  }
  get areCharactersRepeated() {
    return /^(.)\1{3,}$/.test(this.password);
  }
  get areWordsRepeated() {
    return /^(.{4,}).*\1$/.test(this.password);
  }
  get passwordValidationMessages() {
    if (this.areCharactersRepeated)
      return this.$t("login.password-security.no-same-characters");
    if (this.areWordsRepeated)
      return this.$t("login.password-security.no-repeated-words");
    if (this.password.length < 8)
      return this.$t("login.password-security.not-long-enough");
    if (!this.hasUppercaseLetters || !this.hasLowercaseLetters)
      return this.$t("login.password-security.no-uppercase-lowercase");
    if (!this.hasNumbers || !this.hasSymbols)
      return this.$t("login.password-security.no-numbers-and-special");
    return "";
  }

  async setupFinishClicked() {
    try {
      if (this.admin) {
        this.loading = true;
        const response = await authModule.createAdmin({
          username: this.username,
          password: this.password
        });
        this.loading = false;
        console.log("created admin");
        this.$router.replace("/admin");
      } else {
        this.loading = true;
        const response = await authModule.createUser({
          username: this.username,
          password: this.password,
          firstName: this.firstName,
          lastName: this.lastName,
          email: this.email
        });
        const settingsResponse = await settingsModule.saveAllSettings();
        this.loading = false;
        this.$router.replace("/login");
      }
    } catch (err) {
      this.loading = false;
    }
  }

  get language() {
    return settingsModule.language;
  }

  get theme() {
    return settingsModule.theme;
  }

  setLanguage(lang: Language) {
    settingsModule.setLanguage(lang);
  }

  setDark(isDark: boolean) {
    settingsModule.setDark(isDark);
  }

  get availableLanguages() {
    return [
      { text: this.$t("language.german"), value: Language.de },
      { text: this.$t("language.english"), value: Language.en }
    ];
  }
}
